.Input{
    width:100%;
    padding:10px;
    box-sizing: border-box;

}

.Label{
    display: block;
    font-weight: bold;
    margin-bottom: 8px;
}
.InputElement {
    outline: none;
    border: 1px solid #ccc;
    background-color: #ffffff;
    font: inherit;
    padding: 6px 10px;
    display: block;
    width:100%;
    box-sizing: border-box;
}
.InputElement:focus{
    background-color: #ccc;
    outline: none;

}
.Invalid{
    border:1px solid red;
    background-color: rgb(248, 146, 134);

}